@import url("https://fonts.googleapis.com/css2?family=Epilogue:wght@500;700&display=swap");
html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html,
body {
  overflow-x: hidden;
  height: 100%;
}

body {
  margin: 0;
}

h1,
h2,
h3,
h4,
p,
blockquote,
figure,
ol,
ul {
  margin: 0;
  padding: 0;
}

main,
li {
  display: block;
}

ol,
ul {
  list-style: none;
}

h1,
h2,
h3,
h4 {
  font-size: inherit;
}

strong {
  font-weight: bold;
}

a,
[role=button],
.ajax,
.cta {
  color: inherit;
}

a {
  text-decoration: none;
}

button {
  overflow: visible;
  border: 0;
  font: inherit;
  -webkit-font-smoothing: inherit;
  letter-spacing: inherit;
  background: none;
  cursor: pointer;
  padding: 0;
}

::-moz-focus-inner {
  padding: 0;
  border: 0;
}

:focus {
  outline: 0;
}

img {
  max-width: 100%;
  height: auto;
  border: 0;
}

blockquote,
q {
  quotes: none;
}

fieldset {
  border: none;
  padding: 0;
}

body {
  color: hsl(0deg, 0%, 41%);
  overflow: hidden;
  box-sizing: border-box;
  font-family: "Epilogue", sans-serif;
}
body.body-menu {
  background-color: rgba(0, 0, 0, 0.8);
}

.header__wrapper {
  padding: 1rem 1rem 0 1rem;
  position: relative;
}
.header__wrapper-menu {
  height: 100vh;
  width: 15rem;
  position: absolute;
  right: -18.75rem;
  top: 0;
  background-color: hsl(0deg, 0%, 98%);
  padding: 4.875rem 1.3125rem 7.75rem 1.5rem;
}
.header__wrapper-menu .navigation-menu {
  margin-bottom: 2.125rem;
}
.header__wrapper-menu .navigation-menu .navigation .navigation-item .navigation-inner {
  padding: 1.5rem 0 0 1.5rem;
}
.header__wrapper-menu .navigation-menu .navigation .navigation-item .navigation-inner li {
  display: flex;
  align-items: center;
  margin-bottom: 1rem;
}
.header__wrapper-menu .navigation-menu .navigation .navigation-item .navigation-inner li:last-child {
  margin-bottom: 0;
}
.header__wrapper-menu .navigation-menu .navigation .navigation-item .navigation-inner .todo::before {
  content: "";
  background-image: url("data:image/svg+xml,%3Csvg width='14' height='16' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M14 3v12a1 1 0 0 1-1 1H1a1 1 0 0 1-1-1V3a1 1 0 0 1 1-1h1V1a1 1 0 1 1 2 0v1h2V1a1 1 0 1 1 2 0v1h2V1a1 1 0 0 1 2 0v1h1a1 1 0 0 1 1 1Zm-2 3H2v1h10V6Zm0 3H2v1h10V9Zm0 3H2v1h10v-1Z' fill='%23726CEE'/%3E%3C/svg%3E");
  background-repeat: no-repeat;
  height: 1.25rem;
  width: 1.125rem;
  margin-right: 0.9375rem;
}
.header__wrapper-menu .navigation-menu .navigation .navigation-item .navigation-inner .calender::before {
  content: "";
  background-image: url("data:image/svg+xml,%3Csvg width='16' height='16' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M12.667 8.667h-4v4h4v-4ZM11.334 0v1.333H4.667V0h-2v1.333h-1C.75 1.333 0 2.083 0 3v11.333C0 15.25.75 16 1.667 16h12.667C15.25 16 16 15.25 16 14.333V3c0-.917-.75-1.667-1.666-1.667h-1V0h-2Zm3 14.333H1.667V5.5h12.667v8.833Z' fill='%234BB1DA'/%3E%3C/svg%3E");
  background-repeat: no-repeat;
  height: 1.25rem;
  width: 1.125rem;
  margin-right: 0.9375rem;
}
.header__wrapper-menu .navigation-menu .navigation .navigation-item .navigation-inner .reminders::before {
  content: "";
  background-image: url("data:image/svg+xml,%3Csvg width='13' height='17' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M6.408 13.916c-3.313 0-6-1.343-6-3 0-.612.371-1.18 1-1.654V7.916a5 5 0 0 1 3.041-4.6 2 2 0 0 1 3.507-1.664 2 2 0 0 1 .411 1.664 5.002 5.002 0 0 1 3.041 4.6v1.346c.629.474 1 1.042 1 1.654 0 1.657-2.687 3-6 3Zm0 1c.694 0 1.352-.066 1.984-.16.004.054.016.105.016.16a2 2 0 0 1-4 0c0-.055.012-.106.016-.16.633.094 1.29.16 1.984.16Z' fill='%23EDD556'/%3E%3C/svg%3E");
  background-repeat: no-repeat;
  height: 1.25rem;
  width: 1.125rem;
  margin-right: 0.9375rem;
}
.header__wrapper-menu .navigation-menu .navigation .navigation-item .navigation-inner .planning::before {
  content: "";
  background-image: url("data:image/svg+xml,%3Csvg width='16' height='16' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M8 0a8 8 0 1 1 0 16A8 8 0 0 1 8 0Zm0 2.133a5.867 5.867 0 1 0 0 11.734A5.867 5.867 0 0 0 8 2.133ZM8 3.2a4.8 4.8 0 1 1 0 9.6 4.8 4.8 0 0 1 0-9.6Zm-.533 2.667a.533.533 0 0 0-.534.533v2.133c0 .295.24.534.534.534h3.2a.533.533 0 0 0 0-1.067H8V6.4a.533.533 0 0 0-.533-.533Z' fill='%238E4CB6'/%3E%3C/svg%3E");
  background-repeat: no-repeat;
  height: 1.25rem;
  width: 1.125rem;
  margin-right: 0.9375rem;
}
.header__wrapper-menu .navigation-menu .navigation .navigation-item.has-child {
  background-image: url("data:image/svg+xml,%3Csvg width='10' height='6' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='%23686868' stroke-width='1.5' fill='none' d='m1 1 4 4 4-4'/%3E%3C/svg%3E");
  background-repeat: no-repeat;
  background-position-x: 5.625rem;
  background-position-y: 0.5rem;
}
.header__wrapper-menu .navigation-menu .navigation .navigation-item.has-child .navigation-inner {
  display: none;
}
.header__wrapper-menu .navigation-menu .navigation .navigation-item.has-child--active {
  background-image: url("data:image/svg+xml,%3Csvg width='10' height='6' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='%23686868' stroke-width='1.5' fill='none' d='m1 5 4-4 4 4'/%3E%3C/svg%3E");
}
.header__wrapper-menu .navigation-menu .navigation .navigation-item.has-child--active .navigation-inner {
  display: block;
}
.header__wrapper-menu .navigation-menu .navigation li {
  margin-bottom: 1rem;
}
.header__wrapper-menu .navigation-menu .navigation li:last-child() {
  margin-bottom: 0;
}
.header__wrapper-menu a {
  display: block;
  font-size: 1rem;
  line-height: 1.625rem;
  font-weight: 500;
}
.header__wrapper-menu .navigation-login .login {
  margin-bottom: 1rem;
}
.header__wrapper-menu .navigation-login .register {
  border: 1px solid hsl(0deg, 0%, 41%);
  padding: 0.8125rem 0 0.8125rem 0;
  border-radius: 0.875rem;
}
.header__wrapper-menu .navigation-login a {
  text-align: center;
}
.header__wrapper-menu.menu-active {
  transform: translateX(-300px);
}
.header__wrapper-hamburger {
  position: absolute;
  width: 2rem;
  height: 1.125rem;
  z-index: 999;
  top: 1rem;
  right: 1rem;
}
.header__wrapper-hamburger span {
  width: 2rem;
  height: 0.1875rem;
  background-color: hsl(0deg, 0%, 8%);
  display: block;
}
.header__wrapper-hamburger span:nth-child(2) {
  margin-top: 0.375rem;
  margin-bottom: 0.375rem;
}
.header__wrapper-hamburger.open span:nth-child(1) {
  opacity: 0;
}
.header__wrapper-hamburger.open span:nth-child(2) {
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transform: rotate(45deg);
}
.header__wrapper-hamburger.open span:nth-child(3) {
  position: absolute;
  top: 9px;
  -webkit-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  transform: rotate(-45deg);
}